import React, { useState, useEffect } from "react";
import { getBannerAPI, getPlaylistHotAPI } from "../../axios/api";
// 引入swiper
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "./HomePage.css";

export default function HomePage() {
  const [BannerList, setBannerList] = useState([]);
  const [TagList, setTagList] = useState([]);
  const getBannerList = () => {
    const params = {
      type: 0,
    };
    getBannerAPI(params).then((res) => {
      setBannerList(res.banners);
    });
  };
  const getPlayList = () => {
    getPlaylistHotAPI().then((res) => {
      setTagList(res.tags);
    });
  };
  useEffect(() => {
    // console.log("组件已经挂载完毕：ajax，dom操作，定时器，事件挂载")
    getBannerList();
    getPlayList();
  }, []);
  return (
    <div className="content">
      {/* 轮播区块 */}
      <Swiper
        autoplay={{
          delay: 3000, // 默认延迟3s播放下一张
          stopOnLastSlide: false, // 播放到最后一张时停止：否
          disableOnInteraction: true, // 用户操作轮播图后停止 ：是
        }}
        spaceBetween={30}
        loop={true}
        slidesPerView={3}
        onClick={() => {
          console.log("click");
        }}
      >
        {BannerList.map((item) => {
          return (
            <SwiperSlide key={item.imageUrl}>
              <img className="bannerImg" src={item.imageUrl} alt="#"></img>
            </SwiperSlide>
          );
        })}
      </Swiper>
      {/* 展示区块 */}
      <div className="content_main">
        {TagList.map((item) => {
          return <span className="content_main_text">{item.name}</span>;
        })}
      </div>
    </div>
  );
}
