import React from "react";
import { useNavigate, useRoutes } from "react-router-dom";
import routes from "../../routes/routes";
import "./LayoutBar.css";
import { Layout } from "antd";
import MenuBar from "../base/menuBar";
import HeaderBar from "../base/headerBar";
const { Header, Footer, Content } = Layout;

export default function LayoutBar() {
  const ElementRouter = useRoutes(routes);
  const history = useNavigate();
  const headerStyle = {
    textAlign: "center",
    color: "#fff",
    height: 65,
    lineHeight: "65px",
    backgroundColor: "transparent",
  };
  const contentStyle = {
    textAlign: "center",
    minHeight: 120,
    lineHeight: "120px",
    color: "#fff",
  };
  const toBlog = () => {
    window.open("http://118.89.120.183:81/");
  };
  const footerStyle = {
    textAlign: "center",
    color: "aqua",
    lineHeight: "65px",
    height: 65,
    backgroundColor: "transparent",
    padding: "0px",
    transform: "scale(.7)",
  };
  const menuClick = (path) => {
    console.log(path);
    history(path);
  };
  return (
    <div className="main">
      <Layout style={{ backgroundColor: "transparent" }}>
        <Header style={headerStyle}><HeaderBar /></Header>
        <Layout
          style={{ backgroundColor: "transparent" }}
          className="main_content"
          hasSider
        >
          <MenuBar menuClick={menuClick}> </MenuBar>
          <Content style={contentStyle}>{ElementRouter}</Content>
        </Layout>
        <Footer style={footerStyle}>
          <span className="footEmail" onClick={() => toBlog()}>
            Arieshuxu@163.com
          </span>
        </Footer>
      </Layout>
    </div>
  );
}
