import React from "react";
import "./index.css";
import { MyIcon } from "../icon/icon";

export default function MenuBar(props) {
  const menuList = [
    { path: "home", name: "首页", logo: "icon-gouwo" },
    { path: "search", name: "搜索", logo: "icon-sousuo" },
    { path: "ranking", name: "排行", logo: "icon-paihang1" },
    { path: "setting", name: "设置", logo: "icon-shezhi" },
  ];

  const btnClick = (e) => {
    console.log(e);
  };
  const meunClick = (item) => {
    props.menuClick(item.path);
  };
  return (
    <div className="menuBox">
      {menuList.map((item) => (
        <div
          className="menuItem"
          key={item.path}
          onClick={() => btnClick(item)}
        >
          <MyIcon type={item.logo} onClick={() => meunClick(item)}></MyIcon>
        </div>
      ))}
    </div>
  );
}
